window.dom = { //å°è£…ä¸€ä¸ªå¯¹è±¡
    create(string) { //ç”¨äºåˆ›å»ºèŠ‚ç‚¹
        const container = document.createElement("template");
        container.innerHTML = string.trim();
        return container.content.firstChild;
    },
    after(node, node2) { //ç”¨äºæ–°å¢å¼Ÿå¼Ÿ
        node.parentNode.insertBefore(node2, node.nextSibling);
    },
    before(node, node2) { //ç”¨äºæ–°å¢å“¥å“¥
        node.parentNode.insertBefore(node2, node);
    },
    append(parent, node) { //ç”¨äºæ–°å¢å„¿å­
        parent.appendChild(node)
    },
    wrap(node, parent) { //ç”¨äºæ–°å¢çˆ¸çˆ¸
        dom.before(node, parent)
        dom.append(parent, node)
    },
    remove(node) { //ç”¨äºåˆ é™¤èŠ‚ç‚¹
        node.parentNode.removeChild(node)
        return node
    },
    empty(node) { //ç”¨äºåˆ é™¤åä»£
        const array = []
        let temp = node.firstChild
        while (temp) {
            array.push(dom.remove(node.firstChild))
            temp = node.firstChild
        }
        return array
    },
    attr(node, name, value) { // é‡è½½ï¼ˆæ ¹æ®å‚æ•°ä¸ªæ•°å†™ä¸åŒçš„ä»£ç ï¼‰
        if (arguments.length === 3) { //é•¿åº¦ä¸º3å°±è®¾ç½®å±æ€§
            node.setAttribute(name, value)
        } else if (arguments.length === 2) { //é•¿åº¦ä¸º2å°±è¯»å±æ€§
            return node.getAttribute(name)
        }
    },
    text(node, string) { //å†™å…¥æ–‡æœ¬å†…å®¹
        if (arguments.length === 2) {
            if ('innerText' in node) {
                node.innerText = string
            } else {
                node.textContent = string
            }
        } else if (arguments.length === 1) {
            if ('innerText' in node) {
                return node.innerText
            } else {
                return node.textContent
            }
        }
    },
    html(node, string) { //å†™å…¥HTMLå†…å®¹
        if (arguments.length === 2) {
            node.innerHTML = string
        } else if (arguments.length === 1) {
            return node.innerHTML
        }
    },
    style(node, name, value) {
        if (arguments.length === 3) { //å†™å…¥æ ·å¼
            // dom.style(div, 'color', 'red')
            node.style[name] = value
        } else if (arguments.length === 2) {
            if (typeof name === '') { //å¦‚æœnameçš„ç±»å‹æ˜¯å­—ç¬¦ä¸²ï¼Œè¯»æ ·å¼
                // dom.style(div, 'color')
                return node.style[name]
            } else if (name instanceof Object) { //å¦‚æœnameæ˜¯objectçš„å®ä¾‹ï¼Œå†™ä¸€ç»„æ ·å¼
                // dom.style(div, {color: 'red'})
                const object = name
                for (let key in object) {
                    node.style[key] = object[key]
                }
            }
        }
    },
    class: { //å®šä¹‰ä¸€ä¸ªclassç±»
        add(node, className) { //æ·»åŠ ä¸€ä¸ªç±»
            node.classList.add(className)
        },
        remove(node, className) { //åˆ é™¤ä¸€ä¸ªç±»
            node.classList.remove(className)
        },
        has(node, className) { //æ£€æŸ¥æ˜¯å¦æœ‰ä¸€ä¸ªç±»
            return node.classList.contains(className)
        }
    },
    on(node, eventName, fn) { //æ·»åŠ ç›‘å¬äº‹ä»¶
        node.addEventListener(eventName, fn)
    },
    off(node, eventName, fn) { //ç§»é™¤ç›‘å¬äº‹ä»¶
        node.removeEventListener(eventName, fn)
    },
    find(selector, scope) { //ç”¨äºè·å–æ ‡ç­¾æˆ–æ ‡ç­¾ä»¬,scopeæ˜¯æŸ¥æ‰¾èŒƒå›´ï¼Œç¡®å®šåœ¨å“ªé‡Œæ‰¾,æ¯”å¦‚æœ‰ä¸¤ä¸ªdivæœ‰åŒä¸€ä¸ªclass 
        return (scope || document).querySelector(selector)
    },
    parent(node) { //ç”¨äºè·å–çˆ¶å…ƒç´ 
        return node.parentNode
    },
    children(node) { //ç”¨äºè·å–å­å…ƒç´ 
        return node.children
    },
    siblings(node) { //ç”¨äºè·å–å…„å¼Ÿå…ƒç´ 
        return Array.from(node.parentNode.children)
            .filter(n => n !== node)
    },
    next(node) { //ç”¨äºè·å–å¼Ÿå¼Ÿå…ƒç´    
        let x = node.nextSibling
        while (x && x.nodeType === 3) { //åŒ…è£…ä¸‹ä¸€ä¸ªå…ƒç´ æ˜¯element
            x = x.nextSibling
        }
        return x
    },
    previous(node) { //ç”¨äºè·å–å“¥å“¥å…ƒç´ 
        let x = node.previousSibling
        while (x && x.nodeType === 3) {
            x = x.previousSibling
        }
        return x
    },
    each(nodeList, fn) { //ç”¨äºéå†æ‰€æœ‰èŠ‚ç‚¹
        for (let i = 0; i < nodeList.length; i++) {
            fn.call(null, nodeList[i])
        }
    },
    index(node) { //ç”¨äºè·å–æ’è¡Œè€å‡ 
        const list = dom.children(node.parentNode)
        let i
        for (i = 0; i < list.length; i++) {
            if (list[i] === node) {
                break
            }
        }
        return i
    }
};